/* navigation bar and head-wrapper (both flex, 1 breakpoint) */

.head-wrap {
  display: flex;
  flex-direction: column-reverse;
  /* text-align: center; */
}

.nav-main {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  /* text-align: center; */
}

.nav-main li {
  margin-left: 20px;
  margin-top: 20px;
  padding: 0.0rem;
  border: 0px solid #dfdfdf;
  border-radius: 4px;
}


@media (min-width: 600px) {

  .head-wrap {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    /* height: 4rem; */
  }

  .nav-main {
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    background-color: #f8f8f8;
    border: 1px #f8f8f8 solid;
    border-radius: 8px;
    /* height: 4rem; */
  }

  .nav-main li {
    margin: 20px;
    border: 0px solid #dfdfdf;
  }

}